.subhead {
  font-size: 1rem;
  font-weight: bold;
}

h3 {
  font-weight: 600;
}

h2 {
  margin-top: 1em;
  margin-bottom:none;
}

@media (max-width: 500px) {

  .tools {
    font-size: 0.8rem;
  }
  .h1, h1 {
    font-size: 2rem;
  }
}

@media (max-width: 400px) {

  .tools {
    font-size: 0.8rem;
  }
  .h1, h1 {
    font-size: 1.8rem;
  }
  p{
    font-size:0.8rem;
    padding-left:0%;
    padding-right:0%;
  }
}

